<template>
    <div class="business">
        <div class="business-left">
            <h3>超级企业管理({{ this.arr.length }})</h3>
            <div class="business-left-input">
                <el-input style="width: 70%;" placeholder="请输入内容" v-model="input" class="input-with-select">
