                            <el-input v-model="form.ename"></el-input>
                        </el-form-item>
                        <el-form-item label="ip域名" prop="ipstr">
                            <el-input v-model="form.ipstr"></el-input>
                        </el-form-item>
                        <el-form-item label="超级管理员账号" prop="adminname">
                            <el-input v-model="form.adminname"></el-input>
                        </el-form-item>
                        <el-form-item label="邮箱" prop="adminemil">
                            <el-input v-model="form.adminemil"></el-input>
                        </el-form-item>
                        <el-form-item label="最大坐席数" prop="maxnum">
                            <el-input v-model="form.maxnum"></el-input>
                        </el-form-item>
                        <el-form-item label="最大登录并发总数" prop="maxconcurrency">
                            <el-input v-model="form.maxconcurrency"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="submitForm">提交</el-button>
                            <el-button @click="resetForm">重置</el-button>
                        </el-form-item>
                    </el-form>
                </el-dialog>
            </div>
            <el-menu v-for="(item, index) in this.arr" :key="index" :default-active="activeIndex"
                class="el-menu-vertical-demo" @select="handleSelect(item.id)" background-color="#ffffff"
                :collapse-transition="false">
                <el-menu-item>
                    <div style="display: flex; align-items: center;">
                        <!-- 10x10的div -->
                        <div
                            style="width: 10px; height: 10px; border-radius: 50%; background-color: rgb(56, 216, 143); margin-right: 5px;">
                        </div>
                        <!-- 文本内容 -->
                        {{ item.ename }}({{ item.num }})
                    </div>
                </el-menu-item>
            </el-menu>
        </div>
        <div class="business-right">
            <div style="margin-top: 30px; margin-left: 20px;">
                <el-radio-group v-model="tabPosition" style="margin-bottom: 30px;">
                    <el-radio-button label="top" @change.native="toggleBoxShow('top')">超级企业管理</el-radio-button>
                    <el-radio-button label=" right" @change.native="toggleBoxShow('right')">子企业管理</el-radio-button>
                </el-radio-group>
            </div>
            <div v-if="boxshow">
                <div class="business-right-top">
                    <div class="business-right-center">
                        <div class="sixbox" style="text-align: center;">
                            <div style="">
                                <i class="el-icon-office-building" style="font-size: 35PX; 
                                background-color:rgb(93,172,255); 
                                border-radius: 50%;
                                padding: 10px;
                                color: rgb(255, 255, 255)">
                                </i>
                            </div>
                            <div>
                                <strong>{{ this.bussiness.num }}</strong>
                                <br>
                                <span>子企业个数</span>
                            </div>
                        </div>
                        <div class="sixbox" style="text-align: center;">
                            <div>
                                <i class="el-icon-service" style="font-size: 35PX; 
                                background-color:rgb(255,171,45); 
                                border-radius: 50%;
                                padding: 10px;
                                color: rgb(255, 255, 255)"></i>
                            </div>
                            <div>
                                <strong>{{ this.bussiness.maxnum }}</strong>
                                <br>
                                <span>最大坐席数</span>
                            </div>
                        </div>
                        <div class="sixbox" style="text-align: center;">
                            <div>
                                <i class="el-icon-warning-outline" style="font-size: 35PX; 
                            background-color:rgb(80,223,195); 
                            border-radius: 50%;
                            padding: 10px;
                            color: rgb(255, 255, 255)"></i>
                            </div>
                            <div>
                                <strong>{{ this.bussiness.num }}</strong>
                                <br>
                                <span>实际坐席总数</span>
                            </div>
                        </div>
                        <div class="sixbox" style="text-align: center;">
                            <i class=" el-icon-folder-delete" style="font-size: 35PX;
                        background-color:rgb(255,145,113); 
                        border-radius: 50%;
                        padding: 10px;
                        color: rgb(255, 255, 255)"></i>
                            <div style="text-align: center;">
                                <strong>{{ this.bussiness.stopnum }}</strong>
                                <br>
                                <span>停用坐席总数</span>
                            </div>
                        </div>
                        <div class="sixbox" style="text-align: center;">
                            <div>
                                <i class="el-icon-document-copy" style="font-size: 35PX;
                            background-color:rgb(131,232,172); 
                            border-radius: 50%;
                            padding: 10px;
                            color: rgb(255, 255, 255)"></i>
                            </div>
                            <div>
                                <strong>{{ this.bussiness.maxconcurrency }}</strong>
                                <br>
                                <span>最大登录并发总数</span>
                            </div>
                        </div>
                        <div class="sixbox" style="text-align: center;">
                            <div>
                                <i class="el-icon-phone-outline" style="font-size: 35PX;background-color:rgb(254,212,98); 
                                border-radius: 50%;
                                 padding: 10px;
                                  color: rgb(255, 255, 255)"></i>
                            </div>
                            <div>
                                <strong>{{ this.bussiness.yucenum }}</strong>
                                <br>
                                <span>预测外呼坐席总数</span>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="business-right-bottom">
                    <div class="business-right-bottom-top" style="display: flex; justify-content: space-between">
                        <span style="line-height: 40px;">
                            超级企业信息 <i class="el-icon-edit"></i>
                        </span>
                        <p><span>当前超级企业为启用状态<el-button type="text" @click="UpDatabuss">停用超级企业</el-button>&emsp;</span>
                        </p>
                    </div>
                    <div class="business-right-bottom-bottom">
                        <div class="foot-box-left">
                            <p class="foot-p"><span style="color:#F56C6C; margin-right: 2px;">*</span>超级企业名称: <span
                                    class="foot-span">{{
                                    this.bussiness.adminname }}</span>
                            </p>
                            <p class="foot-p"><span style="color:#F56C6C; margin-right: 2px;">*</span>企业域名: <span
                                    class="foot-span">{{
                                    this.bussiness.ipstr }}</span> </p>
                            <p class="foot-p"><span style="color:#F56C6C; margin-right: 2px;">*</span>超级管理员账号: <span
                                    class="foot-span">{{
                                    this.bussiness.adminname }}</span> </p>
                            <p class="foot-p"><span style="color:#F56C6C; margin-right: 2px;">*</span>邮箱: <span
                                    class="foot-span">{{
                                    this.bussiness.adminemil }}</span></p>
                        </div>
                        <div class="foot-box-right">
                            <p class="foot-p"><span style="color:#F56C6C; margin-right: 2px;">*</span>超级企业id: <span
                                    class="foot-span">{{
                                    this.bussiness.id }}</span> </p>
                            <p class="foot-p"><span style="color:#F56C6C; margin-right: 2px;">*</span>最大坐席数: <span
                                    class="foot-span">{{
                                    this.bussiness.maxnum }}</span> </p>
                            <p class="foot-p"><span style="color:#F56C6C; margin-right: 2px;">*</span>最大登录总并发数: <span
                                    class="foot-span">{{
                                    this.bussiness.maxconcurrency }}</span> </p>
                            <p class="foot-p"><span style="color:#F56C6C; margin-right: 2px;">*</span>创建时间: <span
                                    class="foot-span">{{
                                    timestampToDate(this.bussiness.create_time) }}</span> </p>
                        </div>
                    </div>
                </div>
            </div>
            <div v-else>
                <div class="else-top">
                    <span class="el-dropdown-link"
                        style="display: inline-block; margin-left:30px; padding:5px; text-align: center; width:200px; height: 25px;background-color:rgb(233, 243, 251)">
                        客户测<i class="el-icon-arrow-down el-icon--right" style="margin-left:30px"></i>
                    </span>
                    <p><span>当前超级企业为停用状态<el-button type="text" @click="UpDatabuss">停用超级企业</el-button>&emsp;</span>
                    </p>
                </div>
                <div class="else-center">
                    <div>
                        <h1>签单</h1>
                        <p>企业类型</p>
                    </div>
                    <div>
                        <h1>{{ this.bussiness.maxnum }}</h1>
                        <p>最大坐席数</p>
                    </div>
                    <div>
                        <h1>{{ this.bussiness.maxconcurrency }}</h1>
                        <p>最大登录并发数</p>
                    </div>
                    <div>
                        <h1>停用</h1>
                        <p>api功能</p>
                    </div>
                    <div>
                        <h1>启用</h1>
                        <p>录音</p>
                    </div>
                </div>
                <el-tabs v-model="activeName" @tab-click="abc">
                    <el-tab-pane label="用户管理" name="first"></el-tab-pane>
                    <el-tab-pane label="配置管理" name="second">配置管理</el-tab-pane>
                    <el-tab-pane label="角色管理" name="third">角色管理</el-tab-pane>
                    <el-tab-pane label="互转计费配置" name="fourth"></el-tab-pane>
                </el-tabs>
                <div class="else-bottom">
                    <component :is="components[activeName]" :someProp="passedData"></component>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { Getbussiness, AddBussiness, UpDatabuss, Getbussininfo } from "@/api/https";
import FirstComponent from "../components/FirstComponent";
import FourthComponent from "../components/FourthComponent";
export default {
    data() {
        return {
            //组件传参
            passedData: {}, // 初始化参数对象
            //动态组件切换
            components: {
                first: FirstComponent,
                // second: SecondComponent,
                // third: ThirdComponent,
                fourth: FourthComponent
            },
            //切换
            radioSelected: 'top', // 初始化选择的状态，默认为'top'
            boxshow: true, // 根据你的需求初始化boxshow的值
            //详情信息
            bussiness: {},
            arr: [],
            input: '',
            activeIndex: '1',
            dialogVisible: false,
            tabPosition: 'left',
            activeName: 'first',
            form: {
                ename: '',
                ipstr: '',
                adminname: '',
                adminemil: '',
                maxnum: '',
                maxconcurrency: ''
            },
            rules: {
                ename: [{ required: true, message: '企业名称不能为空', trigger: 'blur' }],
                ipstr: [{ required: true, message: 'ip域名不能为空', trigger: 'blur' }],
                adminname: [{ required: true, message: '超级管理员账号不能为空', trigger: 'blur' }],
                adminemil: [{ required: true, message: '邮箱不能为空', trigger: 'blur' }],
                maxnum: [{ required: true, message: '最大坐席数不能为空', trigger: 'blur' }],
                maxconcurrency: [{ required: true, message: '最大登录并发总数不能为空', trigger: 'blur' }]
            },
        }
    },
    methods: {
        //子企业详情
        abc(tab) {
            this.activeName = tab.name;
            switch (tab.name) {
                case 'first':
                    this.passedData = {
                        ename: this.bussiness.ename,
                        maxnum: this.bussiness.maxnum,
                        maxconcurrency: this.bussiness.maxconcurrency,
                        create_time: this.bussiness.create_time,
                    };
                    this.$nextTick(() => {
                        // 在这里访问或操作更新后的 DOM
                        console.log(this.passedData);
                    });
                    break;
                case 'fourth':
                    this.currentPageParams = { otherKey: 'otherValue' };
                    break;
                // 更多case...
            }
        },
        //
        toggleBoxShow(selected) {
            // 根据需要调整条件逻辑，这里仅作为示例
            this.boxshow = selected === 'top'; // 当选择'top'时显示盒子，否则隐藏
        },
        // 重置表单
        resetForm() {
            if (this.$refs.dataForm) {
                this.$refs.dataForm.resetFields();
            }
            this.form = { ename: '', ipstr: "", adminname: "", adminemil: "", maxnum: "", maxconcurrency: "", };
        },
        AddBussiness(data) {
            AddBussiness(data).then(res => {
                console.log(res);
            })
        },
        // 提交表单
        submitForm() {
            this.$refs['dataForm'].validate((valid) => {
                if (valid) {
                    this.AddBussiness(this.form)
                    // 这里可以添加提交表单的逻辑
                    alert('提交成功！');
                    this.dialogVisible = false;
                    this.Getbussiness();
                    this.resetForm();
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        // 打开对话框
        handleClick() {
            this.dialogVisible = true;
        },
        handleSelect(key) {
            Getbussininfo(key).then(res => {
                this.bussiness = res.data
            })
        },
        //获取企业列表
        Getbussiness() {
            Getbussiness().then(res => {
                this.arr = [...res.data]
                this.bussiness = res.data[0]
                this.$forceUpdate();
            })
        },
        //时间戳转换
        timestampToDate(timestamp) {
            // 确保时间戳是毫秒单位，如果传入的是秒单位则乘以1000
            timestamp *= 1000;
            var date = new Date(timestamp);
            var year = date.getFullYear();
            var month = ("0" + (date.getMonth() + 1)).slice(-2); // 月份是从0开始的，所以要加1
            var day = ("0" + date.getDate()).slice(-2);
            var hours = ("0" + date.getHours()).slice(-2);
            var minutes = ("0" + date.getMinutes()).slice(-2);
            var seconds = ("0" + date.getSeconds()).slice(-2);

            return year + "-" + month + "-" + day + " " + hours + ":" + minutes + ":" + seconds;
        },
        //停用企业
        UpDatabuss() {
            this.$confirm('停用超级企业,其子企业也全部停用!', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
                center: true
            }).then(() => {
                console.log(this.bussiness.id, this.bussiness.status);
                UpDatabuss(this.bussiness.id, this.bussiness.status).then(res => {
                    console.log(res);
                })
                this.$message({
                    type: 'success',
                    message: '停用成功!'
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
    },
    mounted() {
        this.Getbussiness();
        this.timestampToDate()
    },
    created() {

    },
}
</script>
